.header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px;
  
    &__icon {
      display: flex;
      align-items: center;
    }
  
    &__text {
      padding-left: 5px;
      font-family: $barlow;
      font-size: 11px;
      font-weight: 300;
      line-height: normal;
      color: $white-50;
      display: inline-block;
      @media screen and (min-width: 768px) {
        font-size: 14px;
      }
    }
  
    &__title {
      font-family: $ChakraPetch;
      font-size: 20px;
      line-height: normal;
      color: $white;
  
      @media screen and (min-width: 768px) {
        font-size: 50px;
      }
    }
  
    &__text2 {
      font-family: $barlow;
      font-size: 14px;
      font-weight: 400;
      font-style: italic;
      // padding-bottom: 5px;
      text-align: center;
      color: rgba(255, 255, 255, 0.6);
      @media screen and (min-width: 768px) {
        font-size: 18px;
      }
    }
  
    &__button {
      background-color: $dark-lilac;
      color: $white;
      border: none;
      border-radius: 20px;
      width: 90px;
      height: 17px;
      font-size: 8px;
      font-family: $barlow;
      cursor: pointer;
      @media screen and (min-width: 768px) {
        width: 154px;
        height: 29px;
        font-size: 15px;
      }
    }
    &__container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      // padding-bottom: 30px;
    }
  
    &__logo {
      height: 35px;
      width: 200px;
  
      &--nasa {
        // height: 35px;
        width: 150px;
        border-radius: 50%;
      }
    }
  
    &__container2 {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 10px;
    }
  }
  
  .fa-sharp {
    font-size: 23px;
    color: $white;
  }

.h1 {
    color: red;
}